import React from "react";
import { NavLink } from "react-router-dom";

const Navbar = () =>{
return (
    <>

    
    <div className="container-fluid nav_bg">
<div className="row">
  <div className="col-10 mx-auto">
  <nav className="navbar navbar-expand-lg navbar-light bg-light" id="nav">
  <div className="container-fluid">
    <NavLink activeClassName='menu-active' className="navbar-brand" to="/">Amdadul Hoq Rana</NavLink>
    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav ml-auto mb-2 mb-lg-0"> 
      <li className="nav-item">
          <NavLink activeClassName='menu-active' className="nav-link text-uppercase" to="/">Home</NavLink>
        </li>
        <li className="nav-item">
          <NavLink activeClassName='menu-active' className="nav-link text-uppercase" to="/about">About</NavLink>
        </li>
        <li className="nav-item">
          <NavLink activeClassName='menu-active' className="nav-link text-uppercase" to="/service">Service</NavLink>
        </li>
        <li className="nav-item">
          <NavLink activeClassName='menu-active' className="nav-link text-uppercase" to="/contact">Contact</NavLink>
        </li>
        
      </ul>
     
    </div>
  </div>
</nav>
  </div>
</div>
    </div>


    </>
);

}

export default Navbar;